*,
*::before,
*::after {
    box-sizing: border-box;
}

body {
    font-family: 'Helvetica Neue', -apple-system, BlinkMacSystemFont, 'Segoe UI',
        Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', sans-serif;
    line-height: 1.5;
    color: #2a2a2a;
}

img {
    display: block;
    max-width: 100%;
    height: auto;
}

input {
    font: inherit;
}

/* STYLES FOR BUTTON  */
button {
    margin: 4px;
    padding: 8px 12px;
    font-size: 16px;
    font-family: inherit;
    cursor: pointer;
}

/* STYLES FOR HIDDEN  */
.hidden {
    display: none;
}

/* ----------------------------------------------------------- */
/* STYLES FOR BACK BTN */

.back-btn {
    font-size: 1rem;
    font-weight: bold;
    text-transform: uppercase;
    text-decoration: none;
    border: none;
    border-radius: 0.5rem;
    color: #fff;
    background-color: #f68768;
    box-shadow: 0 2px 5px rgba(211, 37, 37, 0.7);
    transition: all 0.2s ease-in-out;
    transition: transform 0.2s ease-out, box-shadow 0.2s ease-out;
}

.back-btn:hover {
    transform: translateY(-2px);
    box-shadow: 0 4px 6px rgba(22, 155, 175, 0.7);
}

.back-btn:active {
    transform: translateY(0);
    box-shadow: none;
}

.back-btn__link {
    display: flex;
    align-items: center;
    padding: 0.7rem 1.5rem;
    text-decoration: inherit;
    color: inherit;
}

.back-btn .icon {
    width: 1.5rem;
    height: 1.5rem;
    fill: inherit;
}

/* ----------------------------------------------------------- */
/* STYLES FOR MAGIC BTN */
.magic-btn {
    display: block;
    /* position: relative; */
    padding: 15px 30px;
    background-color: #0bcc95;
    border-radius: 8px;
    border: none;
    color: white;
    font-size: 20px;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 2px;
    overflow: hidden;
    cursor: pointer;
    box-shadow: 0 5px 0 #ef8d8f;
    transition: transform 0.3s;
}

.magic-btn:hover {
    transform: translateY(5px);
    box-shadow: 0 2px 0 #f08d8e;
}

.magic-btn:before,
.magic-btn:after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background: linear-gradient(45deg, rgba(200, 28, 45, 0.7) 0%, transparent 80%);
    top: 0;
    left: 0;
    z-index: -1;
    transition: transform 0.3s;
}

.magic-btn:before {
    transform: rotateX(90deg) translateZ(-40px);
}

.magic-btn:after {
    transform: rotateY(90deg) translateZ(-40px);
}

.magic-btn:hover:before {
    transform: rotateX(0) translateZ(0);
}

.magic-btn:hover:after {
    transform: rotateY(0) translateZ(0);
}

.magic-btn>.icon {
    display: inline-block;
    margin-left: 10px;
    font-size: 24px;
}

/* ----------------------------------------------------------- */
/* STYLES FOR HEADING */

.page-header {
    font-family: 'Poppins', sans-serif;
    font-size: 3.5rem;
    font-weight: 600;
    color: #333;
    text-shadow: 0 2px 2px rgba(0, 0, 0, 0.1);
    text-align: center;
    margin-bottom: 2rem;
    text-transform: uppercase;
    letter-spacing: 1px;
}

.page-header::after {
    content: '';
    display: block;
    width: 100%;
    height: 3px;
    background-color: #5f5f5f;
    margin-top: 1.5rem;
    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.1);
}

/* ----------------------------------------------------------- */
/* STYLES FOR HERO */
.hero {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 2rem;
}

/* ----------------------------------------------------------- */
/* STYLES FOR PARTIALS */

.materials__wrap {
    padding: 1.6rem;
    max-width: 30rem;
}

.materials__header {
    font-size: 2rem;
    font-weight: bold;
    padding-left: 2rem;
    margin-bottom: 1rem;
}

.materials__list {
    list-style: none;
    padding: 0;
    margin: 0;
}

.materials__item {
    margin-bottom: 0.5rem;
}

.materials__link {
    display: block;
    padding: 0.5rem;
    color: #333;
    font-size: 1.2rem;
    text-decoration: none;
    transition: all 0.2s ease-in-out;    
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
}

.materials__link:hover {
    color: #4ae4c2;
    background-image: linear-gradient(to right, #fc354c, #0abfbc);
    transform: translateX(10px);
    box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.5);
}

/* ----------------------------------------------------------- */
/* STYLES FOR FOOTER */

.footer {
    color: #333;
    margin: 2rem 0;
    text-align: center;
    font-size: 0.8rem;
}

.footer::before {
    content: '';
    display: block;
    width: 100%;
    height: 3px;
    background-color: #5f5f5f;
    margin-bottom: 3rem;
    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.1);
}

.footer__container {
    padding: 0 1rem;
}

/* ----------------------------------------------------------- */
/* STYLES FOR SOCIALS */

.social__list {
    padding: 0;
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 0.8rem;
}

.social__item {
    width: 2.75rem;
    height: 2.75rem;
}

.social__link {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    background-color: rgba(255, 255, 255, 0.3);
    width: 100%;
    height: 100%;
    border-radius: 50%;
    transition: all 0.3s ease;
}

.social__link:hover,
.social__link:focus {
    background-image: linear-gradient(to right, #0abfbc, #fc354c);
    color: #fff;
    transform: scale(1.2);
    box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.5);
}

.social__icon {
    fill: currentColor;
    width: 45%;
    height: 45%;
}

/* ----------------------------------------------------------- */
/* STYLES FOR BTN TO TOP */

.to-top-btn {
    position: fixed;
    right: 2rem;
    bottom: 2rem;
    width: 2.75rem;
    height: 2.75rem;
    border-radius: 50%;
    background-color: rgba(255, 255, 255, 0.3);
    color: #fff;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
}

.to-top-btn:hover {
    background-image: linear-gradient(to right, #0abfbc, #fc354c);
}

.to-top-btn__icon {
    /* display: block; */
    /* margin: 0 auto; */
    width: 100%;
    height: 100%;
    fill: currentColor;
    transition: transform 0.3s ease-in-out;
}

.to-top-btn:hover .to-top-btn__icon {
    transform: scale(2);
}

/* ----------------------------------------------------------- */
/* STYLES FOR COUNTER */

.counter {
    display: flex;
    align-items: center;
} 

.counter>p {
    margin: 0;
    font-size: 48px;
    font-family: monospace;
}

/* ----------------------------------------------------------- */
.site-nav_link {
    color: currentColor;
text-decoration: none;
}

.site-nav_link:hover {
    text-decoration: underline;
}

.site-nav__link--current {
    color: tomato;
}

/* ----------------------------------------------------------- */

.color-picker__option {
    display: inline-block;
width: 40px;
height: 40px;
margin: 4px;
border: none;
outline: none;
cursor: pointer;
}

.transaction-table {
    border-collapse: collapse;
}

.transaction-table th,
.transaction-table td {
    padding: 8px;
    border: 1px solid #2a2a2a;
}

.title__link {
    display: inline-flex;
    margin-left: 40px;
}

input {
    font: inherit;
}

.section {
    padding-top: 40px;
    padding-bottom: 40px;
}